<template>
  <div class="search">
    <el-form label-position="left" label-width="76px" :model="formData">
      <el-row :gutter="40">
        <el-col :span="7">
          <el-form-item label="团队名称">
            <el-input
              v-model="formData.name"
              class="fd-team-manage__header__item fd-team-manage__header__search"
              size="small"
              placeholder="请输入团队名称"
              clearable
            />
          </el-form-item>
        </el-col>
        <el-col
          :span="6"
          v-if="teamType === 'BASIC_ERKE' || teamType === 'BASIC_CHANKE'"
        >
          <el-form-item label="医生姓名">
            <el-input
              v-model="formData.doctor"
              placeholder="请输入医生姓名"
              size="small"
              clearable
            />
          </el-form-item>
        </el-col>
        <el-col :span="5" v-if="teamType === 'COMMON'">
          <el-form-item label="团队成员">
            <el-input
              v-model="formData.commonMember"
              placeholder="请输入团队成员姓名"
              size="small"
              clearable
            />
          </el-form-item>
        </el-col>
        <el-col :span="5" v-if="teamType === 'COMMON'">
          <el-form-item label="团队类型">
            <el-select
              size="small"
              v-model="formData.teamCategory"
              placeholder="请选择团队类型"
            >
              <el-option
                v-for="item in $store.state.dictionary.teamCategories"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-button type="primary" size="small" @click="handleSearch"
            >搜索</el-button
          >
          <el-button type="primary" size="small" plain @click="handleReset"
            >重置</el-button
          >
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>

<script>
const defaultformData = {
  name: '',
  doctor: '',
  commonMember: '',
  teamCategory: ''
}

export default {
  props: {
    localFormData: {
      type: Object,
      default: () => {}
    },
    teamType: {
      type: String,
      default: 'BASIC_ERKE'
    }
  },

  data() {
    return {
      formData: {}
    }
  },

  created() {
    this.formData = Object.assign({}, defaultformData, this.localFormData)
  },

  methods: {
    handleSearch() {
      this.$emit('search', this.formData)
    },
    handleReset() {
      this.handleResetFormData()
      this.handleSearch('reset')
    },
    handleResetFormData() {
      this.formData = Object.assign({}, defaultformData)
    }
  }
}
</script>
