<template>
  <div class="fd-team-manage">
    <el-tabs v-model="activeLabel" class="professional-role__header__tabs">
      <el-tab-pane
        v-for="{ label, name } in $store.state.dictionary.fdTeamTypes"
        :key="name"
        :name="name"
      >
        <span
          slot="label"
          class="professional-role__header__tabs__slot-label"
          >{{ label }}</span
        >
      </el-tab-pane>
    </el-tabs>
    <div class="fd-team-manage__header margin-top-20">
      <search-form
        @search="onSearchClick"
        :team-type="activeLabel"
        ref="searchForm"
      />
    </div>
    <el-button type="primary" size="small" @click="handleAddTeam"
      >新增团队</el-button
    >

    <div class="fd-team-manage__body">
      <div class="el-card-warpper fd-team-manage__body__list">
        <el-card class="el-card--no-shadow el-card--header-nbb el-card--flex">
          <div slot="header">
            <span class="el-card__header__title"
              >家庭医生{{ activeLabel | fdTeamType }}列表</span
            >
          </div>
          <el-table
            v-loading="loading"
            class="fd-team-manage__body__list__table sbl-table"
            :data="tableDate"
          >
            <el-table-column
              align="center"
              label="团队编号"
              min-width="100"
              prop="id"
              fixed
              key="1"
            />
            <el-table-column
              align="center"
              label="团队名称"
              min-width="100"
              prop="name"
              key="2"
            />
            <el-table-column
              align="center"
              :label="activeLabel === 'BASIC_ERKE' ? '儿科医生' : '产科医生'"
              prop="doctor"
              min-width="100"
              v-if="
                activeLabel === 'BASIC_ERKE' || activeLabel === 'BASIC_CHANKE'
              "
              key="3"
            >
            </el-table-column>
            <el-table-column
              align="center"
              label="健康管理师"
              prop="hms"
              min-width="140"
              v-if="activeLabel === 'BASIC_ERKE'"
              key="4"
            >
            </el-table-column>
            <el-table-column
              align="center"
              v-if="activeLabel === 'COMMON'"
              label="团队类型"
              prop="category"
              min-width="100"
              key="5"
            />
            <el-table-column
              align="center"
              v-if="activeLabel === 'COMMON'"
              label="团队成员"
              min-width="100"
              prop="commonMembers"
              key="6"
            >
            </el-table-column>
            <el-table-column
              align="center"
              label="创建时间"
              min-width="140"
              prop="createDate"
              key="7"
            >
            </el-table-column>
            <el-table-column
              align="center"
              label="服务状态"
              min-width="100"
              key="8"
              prop="isOnServer"
            >
              <template slot-scope="scope">
                <div>
                  <el-switch
                    :value="scope.row.isOnServer"
                    :width="60"
                    :class="
                      scope.row.isOnServer === true
                        ? 'online-style'
                        : 'offline-style'
                    "
                    :active-text="scope.row.isOnServer ? '' : ''"
                    :inactive-text="
                      scope.row.isOnServer === true ? '上线' : '下线'
                    "
                    inactive-color="#C0C4CC"
                    @change="
                      changeOnlineStatusFun(scope.row, scope.row.isOnServer)
                    "
                  />
                </div>
              </template>
            </el-table-column>
            <el-table-column
              align="center"
              min-width="130"
              prop="contractCounts"
              key="9"
            >
              <template slot="header">
                <span>
                  <span> 当前服务中订单数</span>
                  <el-tooltip
                    class="item color-576277"
                    effect="dark"
                    content="非实时数据，每小时刷新一次"
                    placement="top"
                  >
                    <i class="el-icon-info" />
                  </el-tooltip>
                </span>
              </template>
            </el-table-column>
            <el-table-column
              align="center"
              label="签约饱和状态"
              min-width="180"
              prop="saturation"
              key="10"
            >
              <template slot-scope="scope">
                <div>
                  <div v-if="activeLabel === 'BASIC_ERKE'">
                    <p>
                      医生签约{{
                        scope.row.notOverflow.doctor ? '正常' : '饱和'
                      }}
                    </p>
                    <p>
                      健康管理师签约{{
                        scope.row.notOverflow.hms ? '正常' : '饱和'
                      }}
                    </p>
                  </div>
                  <div v-else-if="activeLabel === 'COMMON'">
                    <p>
                      签约{{ scope.row.notOverflow.common ? '正常' : '饱和' }}
                    </p>
                  </div>
                  <el-button
                    type="text"
                    @click="seeResourceDetail(scope.row._id)"
                    >详情</el-button
                  >
                </div>
              </template>
            </el-table-column>
            <el-table-column
              align="center"
              label="操作"
              fixed="right"
              width="170"
              key="11"
            >
              <template slot-scope="scope">
                <div>
                  <el-button
                    type="text"
                    style="margin-left:10px;"
                    @click="handleEdit(scope)"
                    >编辑</el-button
                  >
                  <!--此处有操作日期按钮，暂时删掉-->
                </div>
              </template>
            </el-table-column>
          </el-table>
          <div class="fd-team-manage__body__list__pagination">
            <el-pagination
              :current-page="page.currentPage"
              :page-sizes="pageSizes"
              :page-size="page.pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="page.total"
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
            />
          </div>
        </el-card>
      </div>
    </div>
    <el-dialog
      :title="dialogTitle"
      :visible.sync="onlineDialogVisible"
      :before-close="cancelOnlineChangeFun"
      width="30%"
      center
    >
      <span>{{ dialogContent }}</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancelOnlineChangeFun">取 消</el-button>
        <el-button type="primary" @click="confirmChangeOnlineStatus">{{
          dialogConfirmBtnTxt
        }}</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="签约饱和状态详情"
      :visible.sync="dialogTableVisible"
      width="500px"
    >
      <el-table :data="fdResourceDetail">
        <el-table-column prop="name" label="成员姓名"></el-table-column>
        <el-table-column prop="busRole" label="成员类型">
          <template slot-scope="scope">
            {{ systemRole(scope.row.busRole, '') }}
          </template>
        </el-table-column>
        <el-table-column prop="fdResource" label="服务资源占用情况">
          <template slot="header">
            <span>
              <span> 服务资源占用情况</span>
              <el-tooltip
                class="item color-576277"
                effect="dark"
                content="已占用的服务资源/服务上限值"
                placement="top"
              >
                <i class="el-icon-info" />
              </el-tooltip>
            </span>
          </template>
          <template slot-scope="scope">
            <span>
              {{ scope.row.fdResourceCurr }} / {{ scope.row.fdResource }}
            </span>
          </template>
        </el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogTableVisible = false"
          >关 闭</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { getList, changeOnlineStatus, getEditInfo } from '@/api/group'

import SearchForm from './search-form'

import { teamCategory } from '@/filters'
import { sectionOffice, systemRole } from '@/utils/dictionary'

export default {
  components: { SearchForm },
  data() {
    return {
      dialogTableVisible: false,
      loading: false,
      activeLabel: 'BASIC_ERKE',
      activeName: '',
      dialogTitle: '上线提示',
      dialogConfirmBtnTxt: '确认上线',
      dialogContent: '',
      onlineDialogVisible: false,
      tableDate: [],
      page: {
        total: 0,
        pageSize: 10,
        currentPage: 1
      },
      is_on_server: false,
      pageSizes: [10, 50, 100],
      fdResourceDetail: []
    }
  },
  created() {
    if (this.$route.params.type) {
      this.activeLabel = this.$route.params.type
    } else {
      this.handleSearch()
    }
  },
  watch: {
    activeLabel() {
      // 根据团队类型搜索列表
      this.handleSearch()
    }
  },
  methods: {
    seeResourceDetail(id) {
      getEditInfo(id).then(res => {
        const { data } = res
        const doctorsResource = data.doctor
          ? [
              {
                name: data.doctor.name,
                busRole: data.doctor.bus_role,
                fdResourceCurr: data.doctor.fd_resource_curr,
                fdResource: data.doctor.fd_resource
              }
            ]
          : []
        const hmsResourceList =
          (data.hms &&
            data.hms.map(hm => {
              return {
                name: hm.name,
                busRole: hm.bus_role,
                fdResourceCurr: hm.fd_resource_curr,
                fdResource: hm.fd_resource
              }
            })) ||
          []

        const membersResourceList =
          (data.common_members &&
            data.common_members.map(member => {
              return {
                name: member.name,
                busRole: member.bus_role,
                fdResourceCurr: member.fd_resource_curr,
                fdResource: member.fd_resource
              }
            })) ||
          []
        this.fdResourceDetail = doctorsResource.concat(
          hmsResourceList,
          membersResourceList
        )
        this.dialogTableVisible = true
      })
    },
    handleAddTeam() {
      this.$router.push({
        name: 'FDTeamAdd',
        params: {
          type: this.activeLabel
        }
      })
    },
    sectionOffice,
    systemRole,
    cancelOnlineChangeFun() {
      this.onlineDialogVisible = false
    },
    changeOnlineStatusFun(row, status) {
      this.onlineDialogVisible = true
      this.activeId = row._id
      this.activeName = row.name
      if (!status || status === 'false') {
        this.is_on_server = true
        this.dialogTitle = '上线提示'
        this.dialogContent =
          '上线后，' + row.name + '将拥有签约用户的权限，确认上线吗？'
        this.dialogConfirmBtnTxt = '确认上线'
      } else {
        this.is_on_server = false
        this.dialogTitle = '下线提示'
        this.dialogContent =
          '下线后，' + row.name + '将不可以签约用户，确认下线吗？'
        this.dialogConfirmBtnTxt = '确认下线'
      }
    },
    confirmChangeOnlineStatus() {
      const params = {
        status: this.is_on_server
      }
      if (!this.activeId) {
        return
      }
      changeOnlineStatus(this.activeId, params).then(res => {
        this.activeId = ''
        this.onlineDialogVisible = false
        this.$message.success('状态改变成功')
        this.handleSearch()
      })
    },
    setTableData({ list }) {
      this.tableDate = list.map((item, index) => ({
        id: item.no,
        _id: item._id ? item._id : index,
        name: item.name ? item.name : '-',
        department: this.sectionOffice(item.section_office, '', true),
        doctor: item.doctor ? item.doctor.name : '无',
        hms: item.hms ? item.hms.map(hm => hm.name).join(',') : '无',
        createDate: item.created_at ? this.formatTime(item.created_at) : '-',
        isOnServer: item.status,
        notOverflow: {
          doctor: item.doctor && !item.doctor.overflow,
          hms: item.hms && item.hms.some(hm => !hm.overflow),
          common:
            item.common_members &&
            item.common_members.some(member => !member.overflow)
        },
        contractCounts: item.contract_counts,
        category: teamCategory(item.category),
        commonMembers: item.common_members
          ? item.common_members.map(member => member.name).join(',')
          : '无'
      }))
    },
    onSearchClick(pageQuery) {
      this.resetPage()
      this.handleSearch(pageQuery)
    },
    resetPage() {
      this.page.currentPage = 1
      this.page.pageSize = 10
    },
    async handleCurrentChange(page) {
      this.page.currentPage = page
      await this.handleSearch(this.$refs.searchForm.formData)
    },

    async handleSizeChange(pageSize) {
      this.page.currentPage = 1
      this.page.pageSize = pageSize
      await this.handleSearch(this.$refs.searchForm.formData)
    },
    async handleSearch(pageQuery = {}) {
      this.loading = true
      const _query = {}
      _query.type = this.activeLabel
      _query.currentPage = this.page.currentPage
      _query.pageSize = this.page.pageSize
      if (pageQuery.name) _query.name = pageQuery.name
      if (pageQuery.doctor) _query.doctor_name = pageQuery.doctor
      if (pageQuery.commonMember) _query.member_name = pageQuery.commonMember
      if (pageQuery.teamCategory) _query.category = pageQuery.teamCategory
      const { data } = await getList(_query)
      this.loading = false
      this.setTableData({ ...data })
      this.page.total = data.page.total
    },
    handleEdit({ row }) {
      this.$router.push({
        name: 'FDTeamEdit',
        params: { id: row._id, type: this.activeLabel }
      })
    },
    handleHistory({ row, $index }) {
      this.$router.push({
        path: `/system-settings/fd-team/${
          row._id
        }/log?name=${encodeURIComponent(row.team.doctor.name)}`
      })
    }
  }
}
</script>
<style lang="scss">
.fd-team-manage {
  .el-card__header {
    margin-bottom: 16px;
  }
  &__body {
    &__list {
      .el-card__body {
        height: calc(100% - 72px);
      }
    }
  }
  .el-form-item {
    &__label,
    &__content {
      line-height: 32px;
    }
  }
}
</style>

<style lang="scss" scoped>
@import '~@/styles/mixin.scss';
@import '~@/styles/variables.scss';

.fd-team-manage {
  min-height: 100%;
  &__header {
    &__search {
      margin-right: 30px;
      width: 220px;
    }
  }
  &__body {
    padding-top: 30px;
    height: calc(100% - 32px);
    &__list {
      overflow: auto;
      height: 100%;
      .el-card {
        height: 100%;
      }
      .sbl-table {
        &__name-col {
          display: inline-flex;
          align-items: center;
          line-height: 0;
          &__name {
            padding-right: 10px;
          }
          &__icon {
            font-size: 18px;
            color: #5ba5e8;
            cursor: pointer;
          }
        }
      }
      &__pagination {
        padding: 60px 0 40px;
        text-align: center;
      }
    }
    &__list-handle {
      display: inline-block;
      min-width: 110px;
      text-align: left;
    }
  }
}
</style>
